.App {
  text-align: center;
}

.App-logo {
  height: 40vmin;
  pointer-events: none;
}

@media (prefers-reduced-motion: no-preference) {
  .App-logo {
    animation: App-logo-spin infinite 20s linear;
  }
}

.App-header {
  background-color: #282c34;
  min-height: 100vh;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  font-size: calc(10px + 2vmin);
}

.App-link {
  color: #61dafb;
}

@keyframes App-logo-spin {
  from {
    transform: rotate(0deg);
  }
  to {
    transform: rotate(360deg);
  }
}
h1{
  font-size: 2rem;
}
.navbar{
  display: flex;
  align-items: center;
  justify-content: center;
  gap:30rem;
  position: relative;
}
.subnav{
  display: flex;
  align-items: center;
  flex-direction: row;
  gap: 2rem;
  list-style:none;
}
a:-webkit-any-link{
  text-decoration: none;
  font-size: 1.2rem;
}
.Menuitems{
  display: flex;
  justify-content: center;
  justify-content: space-between;
  flex-wrap: wrap;
}
.Menu{
  padding-left: 3rem;
}
.item{
  flex-basis: 30rem;
}
.menulist{
  text-align: center;
  color: rgba(5,5,24,0.836);
}
.menusub{
  text-align: center;
  color: rgba(253,102,56,0.9)
}
.itemname{
  color: rgba(253,102,56,0.9) 
}
.Home{
  display: flex;
  justify-content: center;
  gap: 3rem;
}
.homeitem{
  display: flex;
  padding-left: 5rem;
  flex-direction: column;
  justify-content: center;
}
.hometitle{
  font-size: 1.5rem;
  color: darkgray;
}
.homesub{
  font-size: 3rem;
}
.btn {
  display: inline-block;
  padding: 10px 20px;
  font-size: 16px;
  color: #fff;
  width: 10rem;
  background-color: rgba(253,102,56,0.9);
  border: none;
  border-radius: 5px;
  box-shadow: 0px 4px 0px rgba(253,102,56,0.9);
  transition: all 0.2s ease-in-out;
}

.btn:hover {
  background-color: rgba(248, 73, 19, 0.9);
  box-shadow: 0px 4px 0px rgba(253,102,56,0.9);
  transform: translateY(-2px);
}

.btn:active {
  background-color: rgba(253,102,56,0.9);
  box-shadow: none;
  transform: translateY(2px);
}
/* Style the "About Us" section */
#about-us {
  max-width: 800px;
  margin: 0 auto;
  padding: 40px;
  background-color: rgba(255, 165, 138, 0.9);
  border-radius: 20px;
  box-shadow: 0px 0px 10px rgba(0, 0, 0, 0.1);
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
}

/* Style the subheadings */
h2, h3 {
  font-size: 28px;
  font-weight: bold;
  margin-bottom: 20px;
  text-align: center;
  color: rgb(202, 89, 89);
}

h3 {
  font-size: 24px;
  margin-top: 40px;
  color: rgb(202, 89, 89);
}

/* Style the paragraphs */
p {
  font-size: 18px;
  line-height: 1.5;
  margin-bottom: 20px;
  color: black;
  text-align: center;
}
.contact{
  display: flex;
  justify-content: center;
  gap: 7rem;
}
.email{
  display: block;
  display: flex;
  flex-direction: column;
  justify-content: center;
}
.emailsub{
  margin: auto;
}
.email-input-wrapper {
  position: relative;
  display: flex;
  align-items: center;
}

input[type="email"] {
  padding: 10px;
  border: 2px solid #ccc;
  font-size: 16px;
  box-sizing: border-box;
}
button[type="submit"] {
  background-color: rgba(253,102,56,0.9);
  color: #fff;
  font-size: 16px;
  height: 2.6rem;
  border: none;
  padding: 10px 20px;
  cursor: pointer;
  transition: background-color 0.2s ease;
}

button[type="submit"]:hover {
  background-color:rgba(253,102,56,0.9);
  box-shadow: 0px 4px 0px rgba(253,102,56,0.9);
  transform: translateY(-2px);
}

button[type="submit"]::before {
  content: "\2192";
  margin-right: 10px;
}

@media (max-width: 768px) {
  input[type="email"] {
    margin-right: 0;
    margin-bottom: 10px;
  }
}
a,.navbarh1{
  display: block;
  color: rgb(65, 64, 64);
  text-decoration: none;
  font-weight: bold;
}
a:hover {
color: rgba(255, 157, 127, 0.9);
}